@use "../mixins.scss";
@use "../variables.scss";

.products__main-title {
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 1;
  color: variables.$titles;
  margin: 0;
  padding-top: 27px;
  padding-bottom: 41px;
  position: relative;

  @media (min-width: variables.$tablet-width) {
    font-size: 60px;
    line-height: 1;
    padding-top: 65px;
    padding-bottom: 90px;
  }

  @media (min-width: variables.$desktop-width) {
    padding-top: 74px;
    padding-bottom: 165px;
  }
}

@media (max-width: variables.$mobile-width-only) {
  .products__main-title::before {
    content: "";
    position: absolute;
    top: 0;
    left: -20px;
    height: 1px;
    background-color: variables.$borders;
    width: 100vw;
    box-shadow: -100vw 0 variables.$borders;
  }

  .products__main-title::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: -20px;
    height: 1px;
    background-color: variables.$borders;
    width: 100vw;
    box-shadow: -100vw 0 variables.$borders;
  }
}

@media (min-width: variables.$desktop-width) {
  .products__wrapper--extra {
    display: grid;
    grid-template-columns: 1fr 250px;
    column-gap: 80px;
    padding-bottom: 85px;
  }
}

.products__list {
  @include mixins.list-reset;

  @media (min-width: variables.$tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
    row-gap: 141px;
  }

  @media (min-width: variables.$desktop-width) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 80px;
    row-gap: 143px;
  }
}

.products__list--extra {
  display: flex;
  flex-direction: column;
  position: relative;
  padding-bottom: 35px;

  @media (min-width: variables.$tablet-width) {
    padding-bottom: 69px;
    gap: 0;
  }

  @media (min-width: variables.$desktop-width) {
    padding: 0;
  }
}

@media (max-width: variables.$mobile-width-only) {
  .products__list--extra::before {
    content: "";
    position: absolute;
    top: 0;
    left: -20px;
    height: 1px;
    background-color: variables.$borders;
    width: 100vw;
    box-shadow: -100vw 0 variables.$borders;
  }
}

.products__card {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  position: relative;
  padding-top: 19px;
  padding-bottom: 25px;

  @media (min-width: variables.$tablet-width) {
    grid-template-columns: 1fr 1fr;
    background-color: variables.$bg;
    padding: 38px;
  }
}

.products__image {
  grid-column: 1/2;
  grid-row: 1/5;
  justify-self: center;
  align-self: center;
}

@media (max-width: variables.$mobile-width-only) {
  .products__card::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: -20px;
    height: 1px;
    background-color: variables.$borders;
    width: 100vw;
    box-shadow: -100vw 0 variables.$borders;
  }
}

.products__card--extra {
  grid-template-columns: 1fr 1fr;

  @media (min-width: variables.$tablet-width) {
    background-color: variables.$text-alt;
    outline: 1px solid variables.$borders-alt;
    display: flex;
    flex-wrap: wrap;
  }

  @media (min-width: variables.$desktop-width) {
    align-items: center;
    padding: 15px 0 17px 0;
    outline: none;
    border-bottom: 1px solid variables.$borders-alt;
  }
}

@media (min-width: variables.$desktop-width) {
  .products__card--extra:first-child {
    border-top: 1px solid variables.$borders-alt;
  }
}

@media (min-width: variables.$tablet-width) {
  .products__card img {
    grid-column: 1/-1;
  }
}

.products__subtitle {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  color: variables.$titles-extra;
  margin: 0;
  grid-column: 2/4;
  width: 80px;
  padding-bottom: 14px;

  @media (min-width: variables.$tablet-width) {
    grid-column: 1/-1;
    font-size: 20px;
    line-height: 1.2;
    width: 150px;
    text-align: center;
    justify-self: center;
    align-self: center;
  }
}

.products__subtitle--extra {
  grid-column: 1/3;
  width: 150px;

  @media (min-width: variables.$tablet-width) {
    margin-right: auto;
  }

  @media (min-width: variables.$tablet-width) {
    margin-right: auto;
    text-align: left;
  }
}

.products__feature-name {
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  text-align: left;
  color: variables.$text;
  margin: 0;

  @media (min-width: variables.$tablet-width) {
    font-size: 16px;
    line-height: 1;
    padding-top: 7px;
    padding-bottom: 9px;
    border-bottom: 1px solid variables.$borders-alt;
  }
}

.products__feature-value {
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  text-align: right;
  color: variables.$text;
  margin: 0;

  @media (min-width: variables.$tablet-width) {
    font-size: 16px;
    line-height: 1;
    padding-bottom: 9px;
    padding-top: 7px;
    border-bottom: 1px solid variables.$borders-alt;
  }
}

.products__feature-value--extra {
  text-align: left;

  @media (min-width: variables.$tablet-width) {
    border: none;
  }

  @media (min-width: variables.$desktop-width) {
    width: 245px;
  }
}

.products__feature-value--price {
  @media (min-width: variables.$tablet-width) {
    border: none;
    margin-left: 48px;
  }

  @media (min-width: variables.$desktop-width) {
    width: 80px;
    text-align: left;
    margin-left: 0;
  }
}

.products__button {
  @include mixins.button;
  grid-column: 1/4;
  margin-top: 14px;
  font-size: 16px;
  line-height: 1.25;
  padding: 10px 0;
}

@media (min-width: variables.$tablet-width) {
  .products__button {
    grid-column: 1/3;
  }
}

.products__button--extra {
  grid-column: 1/3;

  @media (min-width: variables.$tablet-width) {
    width: 100%;
  }

  @media (min-width: variables.$desktop-width) {
    width: 244px;
    margin-left: 40px;
    margin-top: 0;
  }
}

.products__title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 1;
  text-align: left;
  color: variables.$titles;
  padding-top: 25px;
  padding-bottom: 36px;
  display: flex;
  position: relative;

  @media (min-width: variables.$tablet-width) {
    font-size: 36px;
    line-height: 1;
    padding-left: 37px;
  }
}

.products__title-wrapper {
  display: flex;
  position: relative;
  z-index: 2;
}

.products__title::before {
  content: "";
  position: absolute;
  left: -20px;
  top: 40px;
  height: 2px;
  background-color: variables.$bg-alt;
  width: 100vw;

  @media (min-width: variables.$tablet-width) {
    box-shadow: -100vw 0 variables.$bg-alt;
    left: -32px;
    top: 50px;
  }

  @media (min-width: variables.$desktop-width) {
    box-shadow: -100vw 0 variables.$bg-alt;
    left: -110px;
    top: 50px;
  }
}

.products__title::after {
  content: "";
  position: absolute;
  left: -20px;
  top: 40px;
  height: 4px;
  background-color: variables.$text-alt;
  width: 267px;

  @media (min-width: variables.$tablet-width) {
    width: 409px;
    top: 49px;
    left: 0;
  }
}

.products__promotion {
  position: relative;
  min-height: 290px;
  display: flex;
  flex-direction: column;
  margin-bottom: 35px;

  @media (min-width: variables.$tablet-width) {
    flex-direction: row;
    margin-bottom: 76px;
    min-height: 200px;
  }

  @media (min-width: variables.$desktop-width) {
    margin: 0;
    min-height: 288px;
  }
}

.products__promotion::after {
  content: "";
  background-image: linear-gradient(variables.$bg-alt, variables.$bg-alt), url("../img/catalog-background-cat.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: -1;
}

.products__promotion::before {
  content: "";
  background-image: url("../../img/gift.svg");
  background-repeat: no-repeat;
  width: 78px;
  height: 80px;
  z-index: 2;
  display: block;
  align-self: center;
  margin-top: 59px;
  margin-bottom: 48px;

  @media (min-width: variables.$tablet-width) {
    margin-left: 77px;
    margin-right: auto;
  }
}

.products__promotion-title {
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: variables.$text-alt;
  z-index: 2;
  margin: 0;
  padding: 0 60px;

  @media (min-width: variables.$tablet-width) {
    align-self: center;
    width: 165px;
    padding: 0;
    text-align: left;
    margin-right: 124px;
  }
}
