.main-header__nav {
  position: relative;
  width: auto;
}

.main-header__toggle {
  position: absolute;
  top: 24px;
  right: 0;
  z-index: 1;
  display: block;
  width: 24px;
  height: 24px;
  cursor: pointer;
  border: none;
  padding: 0;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header__nav .main-header__toggle {
  top: -42px;
}

.main-header__nav--closed .main-header__toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  top: 0;
  right: 0;
  background-color: #000000;
  mask: url("../img/menu.svg") no-repeat 50% 50%;
}
.main-header__nav--closed .main-header__toggle::before:hover {
  background-color: rgba(0, 0, 0, 0.8);
}
.main-header__nav--closed .main-header__toggle::before:focus {
  background-color: rgba(0, 0, 0, 0.6);
}
.main-header__nav--closed .main-header__toggle::before:active {
  background-color: rgba(0, 0, 0, 0.6);
}

.main-header__nav--opened .main-header__toggle::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  top: 0;
  right: 0;
  background-color: #000000;
  mask: url("../img/close.svg") no-repeat 50% 50%;
}
.main-header__nav--opened .main-header__toggle::before:hover {
  background-color: rgba(0, 0, 0, 0.8);
}
.main-header__nav--opened .main-header__toggle::before:focus {
  background-color: rgba(0, 0, 0, 0.6);
}
.main-header__nav--opened .main-header__toggle::before:active {
  background-color: rgba(0, 0, 0, 0.6);
}

@media (max-width: 767px) {
  .main-header__nav--closed .main-header__list {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-header__nav--opened .main-header__list {
    position: absolute;
    left: -20px;
    z-index: 2;
    background-color: #ffffff;
    width: 100vw;
  }
}
.main-header__nav--nojs .main-header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-header__nav--nojs .main-header__toggle .main-header__list {
    visibility: visible;
    position: static;
  }
}

